/* Containers */
html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px; }

body {
  width: 60%;
  margin: auto; }
  body .wrapper {
    min-height: calc(100vh - 200px); }

* {
  margin: 0;
  border: 0;
  padding: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

.clearfix::after {
  content: "";
  clear: both;
  display: table; }

/* header */
h1, h2, h3, h4 {
  color: #15402D;
  font-weight: bold; }

h1 {
  font-size: 3em; }

h2 {
  font-size: 2em; }

h3 {
  font-size: 1.5em; }

h4 {
  font-size: 1.2em; }

/* text */
p {
  color: #15402D;
  opacity: 0.8;
  font-size: 1.0em;
  margin-top: 15px; }

/* buttons */
button.primary-btn {
  font-size: 1.0em;
  padding: 15px 10px; }

button:hover {
  transition: opacity linear 0.1s; }

button {
  cursor: pointer; }
  button:hover {
    opacity: 0.5; }
  button.primary-btn {
    color: #fff;
    background-color: #15402D; }
  button.landing-btn {
    padding: 15px 30px;
    color: #fff;
    margin-top: 1.5em;
    margin-bottom: 2em;
    font-size: 1.5em;
    background-color: #15402D; }

/* Landing Section */
.landing {
  background: #eee; }
  .landing .landingWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 40px; }
    .landing .landingWrapper .landingText {
      -webkit-flex: 1 1;
              flex: 1 1;
      margin-top: 5.5em;
      padding-left: 4em;
      padding-right: 4em; }
      .landing .landingWrapper .landingText p {
        font-size: 1.2em;
        opacity: 0.7; }
    .landing .landingWrapper .landingImage {
      -webkit-flex: 1 1;
              flex: 1 1; }
      .landing .landingWrapper .landingImage img {
        float: right;
        width: 100%;
        background-color: #000;
        height: 100%; }
    .landing .landingWrapper ul {
      list-style: none;
      margin-top: 30px;
      line-height: 30px; }
  .landing .bottomScroll {
    height: 50px;
    width: 50px;
    background-color: #333;
    position: absolute;
    bottom: 100px;
    right: 0; }

/* Footer */
footer {
  text-align: center;
  width: 100%;
  height: 60px;
  background-color: #15402D;
  color: #15402D;
  border-top: 1px solid #eee;
  padding-top: 1.2em; }
  footer p {
    color: #fff; }

/* Device Sizes */
/* Desktop */
/* Mobile */
.navigation {
  height: 70px;
  background: #15402D; }

.brand {
  position: absolute;
  padding-left: 20px;
  float: left;
  line-height: 70px;
  text-transform: uppercase;
  font-size: 1.4em; }
  .brand a,
  .brand a:visited {
    color: #ffffff;
    text-decoration: none; }

.nav-container {
  margin: 0 auto; }

nav {
  float: right; }
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    nav ul li {
      float: left;
      position: relative; }
      nav ul li a,
      nav ul li a:visited {
        display: block;
        padding: 0 20px;
        line-height: 70px;
        background: #15402D;
        color: #ffffff;
        text-decoration: none; }
        nav ul li a:hover,
        nav ul li a:visited:hover {
          background: #297F5A;
          color: #ffffff; }
        nav ul li a:not(:only-child):after,
        nav ul li a:visited:not(:only-child):after {
          padding-left: 4px;
          content: ' \25BE'; }
      nav ul li ul li {
        min-width: 190px; }
        nav ul li ul li a {
          padding: 15px 20px;
          line-height: 20px; }

.nav-dropdown {
  position: absolute;
  display: none;
  z-index: 1;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.15); }

/* Mobile navigation */
.nav-mobile {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  background: #15402D;
  height: 70px;
  width: 70px; }

@media only screen and (max-width: 798px) {
  .nav-mobile {
    display: block; }
  .navigation {
    width: 100%;
    position: fixed;
    top: 0px;
    z-index: 1; }
  nav {
    width: 100%;
    padding: 70px 0 15px; }
    nav ul {
      display: none; }
      nav ul li {
        float: none; }
        nav ul li a {
          padding: 15px 20px;
          line-height: 20px;
          font-size: 1.2em; }
        nav ul li ul li a {
          padding-left: 30px; }
        nav ul li:last-child a {
          padding-bottom: 30px; }
  .nav-dropdown {
    position: static; } }

@media screen and (min-width: 799px) {
  .nav-list {
    display: block !important; } }

#nav-toggle {
  position: absolute;
  left: 18px;
  top: 22px;
  cursor: pointer;
  padding: 10px 35px 16px 0px; }
  #nav-toggle span,
  #nav-toggle span:before,
  #nav-toggle span:after {
    cursor: pointer;
    border-radius: 1px;
    height: 5px;
    width: 35px;
    background: #ffffff;
    position: absolute;
    display: block;
    content: '';
    transition: all 300ms ease-in-out; }
  #nav-toggle span:before {
    top: -10px; }
  #nav-toggle span:after {
    bottom: -10px; }
  #nav-toggle.active span {
    background-color: transparent; }
    #nav-toggle.active span:before, #nav-toggle.active span:after {
      top: 0; }
    #nav-toggle.active span:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
    #nav-toggle.active span:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }

@media (max-width: 1280px) {
  body {
    width: 90%; } }

@media (max-width: 1024px) {
  body {
    width: 100%; } }

@media screen and (max-width: 799px) {
  body .wrapper {
    min-height: calc(100vh - 145px);
    margin-top: 70px; }
  .landing .landingWrapper {
    -webkit-flex-direction: column;
            flex-direction: column; }
    .landing .landingWrapper .landingText {
      -webkit-order: 2;
              order: 2;
      padding: 20px;
      margin-top: 1.2em; }
      .landing .landingWrapper .landingText button.landing-btn {
        margin-top: 20px;
        padding-top: 20px;
        margin-bottom: 20px;
        font-size: 1.1em; } }

.App {
  text-align: left; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #fff;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
